<template>
    <div>
        <h2>Create new group</h2>
        <form class="row">
            <div class="col-4">
                <div class="form-group">
                    <label for="group">Name</label>
                    <input type="text" id="group" class="form-control" :class="{'is-invalid' : message}" v-model="group">
                    <div class="invalid-feedback">
                        {{message}}
                    </div>
                </div>
                <div class="btn-group-link">
                    <button class="btn btn-primary" @click="addGroup">Save</button>
                    <button class="btn btn-secondary" @click="gotoAdmin">Cancel</button>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
    import {HTTP} from "../api/api";

    export default {
        name: 'AddGroup',
        data(){
            return{
                group: null,
                message: null
            }
        },
        methods:{
            addGroup(){
                HTTP.post(`add`, {
                    group: this.group,
                    token: this.$parent.token
                })
                    .then(resolve => {
                        this.$router.push({name: 'Admin', params:{
                                send: resolve.data.message
                            }})
                    })
                    .catch(reject => {
                        this.message = reject.response.data.message
                    })
            },
            gotoAdmin(){
                this.$router.push({name: 'Admin'});
            }
        }
    }
</script>

