<template>
    <div>
        <transition name="fade">
            <div class="message" :class="className" v-if="message" :key="timer">
                <span>{{message}}</span>
            </div>
        </transition>
        <div class="item">
            <table class="table table-striped">
                <thead>
                <tr>
                    <th scope="col">#</th>
                    <th scope="col">Group</th>
                    <th scope="col">Point</th>
                    <th scope="col">Prize</th>
                    <th scope="col">Update/Delete</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(group, index) in groups">
                    <th scope="row">{{index + 1}}</th>
                    <td>{{group.group}}</td>
                    <td>
                        <input type="number" class="form-control" v-model="group.point">
                    </td>
                    <td>{{getPrize(index)}}</td>
                    <td>
                        <div class="btn-group-link">
                            <button class="btn btn-sm btn-primary" @click="updateGroup(group)">Update</button>
                            <button class="btn btn-sm btn-danger" @click="askDeleteGroup(group)">Delete</button>
                        </div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
        <div class="item">
            <button class="btn btn-primary" @click="gotoAddGroup">Add group</button>
        </div>

        <transition name="modal">
            <div class="modal" id="modal" role="dialog" v-if="group">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <div class="modal-title">Do you want to delete {{group.group}}?</div>
                        </div>
                        <div class="modal-body">
                            <div class="btn-group-link">
                                <button class="btn btn-sm btn-danger" @click="deleteGroup">Delete</button>
                                <button class="btn btn-sm btn-secondary" @click="endShowDelete()">Cancel</button>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        </transition>


    </div>
</template>

<script>
    import {HTTP} from '../api/api'

    export default {
        name: 'Admin',

        props:{
            send:{
                typeof: String,
                default: null
            }
        },

        data(){
            return{
                groups: null,
                prize: [
                    {
                        id: 1,
                        prize: '30.000 VNĐ'
                    },
                    {
                        id: 2,
                        prize: '20.000 VNĐ'
                    },
                    {
                        id: 3,
                        prize: '10.000 VNĐ'
                    },
                ],
                message: null,
                timer: null,
                className: null,
                group: null
            }
        },
        methods:{
            init(){
                HTTP.get(`groups`)
                    .then(resolve => {
                        this.groups = resolve.data.groups;
                        this.groups.sort(this.compare);
                    })
            },
            gotoAddGroup(){
                this.$router.push({name: 'AddGroup'});
            },
            getPrize(index){
                let prize = this.prize.find(x => x.id === index + 1);
                if (prize)
                    return prize.prize;
                return `5.000 VNĐ`;
            },
            updateGroup(group){
                if (group.point > 99999) {
                    clearTimeout(this.timer);
                    this.message = "Point is so big. Please try againt";
                    this.className = 'danger';
                    this.timer = setTimeout(_=>{
                        this.message = null;
                    }, 5000);
                    return;
                }

                HTTP.post(`update`, {
                    group: group.group,
                    point: group.point,
                    token: this.$parent.token
                })
                    .then(resolve => {
                        this.groups = resolve.data.groups;
                        this.groups.sort(this.compare);
                        clearTimeout(this.timer);
                        this.message = resolve.data.message;
                        this.className = 'success';
                        this.timer = setTimeout(_=>{
                            this.message = null;
                        }, 5000);
                    })
            },
            compare(a, b){
                let comparison = 0;
                if (a.point < b.point)
                    comparison = 1;
                else comparison = -1;
                return comparison;
            },
            askDeleteGroup(group){
                this.group = group;
                document.addEventListener('mousedown', this.listenEndDelete);
            },
            endShowDelete(){
                this.group = null;
            },
            listenEndDelete(ev){
                let modal = document.getElementById('modal');
                if (ev.target === modal){
                    this.endShowDelete();
                    document.removeEventListener('mousedown', this.listenEndDelete);
                }
            },
            deleteGroup(){
                HTTP.post(`delete`, {
                    group: this.group.group,
                    token: this.$parent.token
                })
                    .then(resolve => {
                        clearTimeout(this.timer);
                        this.message = resolve.data.message;
                        this.className = 'success';
                        this.timer = setTimeout(_=>{
                            this.message = null;
                        }, 5000);
                        this.init();
                        this.group = null;
                    })
                    .catch(reject => {
                        clearTimeout(this.timer);
                        this.message = reject.response.data.message;
                        this.className = 'danger';
                        this.timer = setTimeout(_=>{
                            this.message = null;
                        }, 5000);
                        this.group = null;
                    })
            }
        },
        created(){
            if (!this.$parent.username){
                this.$router.push({name: 'Login'})
            }

            setTimeout(_=>{
                if (this.send){
                    clearTimeout(this.timer);
                    this.message = this.send;
                    this.className = 'success';
                    this.timer = setTimeout(_=>{
                        this.message = null;
                    }, 3000);
                }
            }, 100);

            this.init();
        }
    }
</script>

<style>
    .btn-group-link button:first-child{
        margin-right: 5px;
    }
    .message{
        display: inline-flex;
        position: absolute;
        padding: 10px 20px;
        border-radius: 10px;
    }
    .success{
        background-color: greenyellow;
    }
    .danger{
        background-color: red;
    }

    .modal{
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(1, 1, 1, .2);
    }

    .modal-dialog{

        box-shadow: 3px 5px 11px rgba(1, 1, 1, .6);
    }
</style>