<template>
    <header>
        <div class="fixed-top app-header">
            <div class="navbar navbar-light navbar-expand-lg">
                <div class="navbar-brand">IT-Engrisk by Group 8</div>
                <div class="navbar-collapse collapse justify-content-end">
                    <ul class="navbar-nav">
                        <li class="nav-item" v-if="this.$parent.username">
                            <div class="nav-link">
                                {{this.$parent.username}}
                            </div>
                        </li>

                        <li class="nav-item" v-if="this.$parent.username">
                            <a href="#" class="nav-link">
                                <button class="btn btn-sm btn-primary" @click="logout">Logout</button>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </header>
</template>

<script>
    import  {HTTP} from "../api/api";

    export default {
        name: 'Header',

        methods:{
            logout(){
                HTTP.post(`logout`, {
                    token: this.$parent.token
                })
                    .then(_ => {
                        this.$parent.username = null;
                        this.$parent.token = null;
                        this.$router.push({name: 'Login'});
                    })
            }
        }
    }
</script>

<style>
    .app-header{
        background-color: whitesmoke;
        box-shadow: 3px 5px 11px rgba(1, 1, 1, 0.6);
    }
</style>