<template>
    <div>
        <transition name="fade">
            <div class="message" :class="className" v-if="message" :key="timer">
                <span>{{message}}</span>
            </div>
        </transition>
        <table class="table table-striped">
            <thead>
            <tr>
                <th scope="col">#</th>
                <th scope="col">Group</th>
                <th scope="col">Point</th>
                <th scope="col">Prize</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(group, index) in groups">
                <th scope="row">{{index + 1}}</th>
                <td>{{group.group}}</td>
                <td>
                    {{group.point}}
                </td>
                <td>{{getPrize(index)}}</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script>
    import {HTTP} from '../api/api'

    export default {
        name: 'Index',

        data() {
            return {
                groups: null,
                prize: [
                    {
                        id: 1,
                        prize: '30.000 VNĐ'
                    },
                    {
                        id: 2,
                        prize: '20.000 VNĐ'
                    },
                    {
                        id: 3,
                        prize: '10.000 VNĐ'
                    },
                ],
                message: null,
                timer: null,
                className: null,
                requestTime: 10000
            }
        },
        methods:{
            init(){
                HTTP.get(`groups`)
                    .then(resolve => {
                        this.requestTime = 10000;

                        this.groups = resolve.data.groups;
                        this.groups.sort(this.compare);

                        clearTimeout(this.timer);
                        this.message = "Data updated";
                        this.className = 'success';
                        this.timer = setTimeout(_=>{
                            this.message = null;
                        }, 3000);
                    })
                    .catch(_ => {
                        this.requestTime = 60000;
                        clearTimeout(this.timer);
                        this.message = "Too Many Requests";
                        this.className = 'danger';
                        this.timer = setTimeout(_=>{
                            this.message = null;
                        }, 3000);
                    })
            },
            getPrize(index){
                let prize = this.prize.find(x => x.id === index + 1);
                if (prize)
                    return prize.prize;
                return `5.000 VNĐ`;
            },
            compare(a, b){
                let comparison = 0;
                if (a.point < b.point)
                    comparison = 1;
                else comparison = -1;
                return comparison;
            }
        },
        created(){
            this.init();

            setInterval(_=>{
                this.init()
            }, this.requestTime);
        }
    }
</script>

