import VueRouter from "vue-router";
import Index from '../views/Index';
import Admin from '../views/Admin';
import AddGroup from "../views/AddGroup";
import Login from '../views/Login'

export default new VueRouter({
    routes: [
        {
            path: '/',
            component: Index,
            name: 'Index'
        },
        {
            path: '/admin',
            component: Admin,
            name: 'Admin',
            props: true
        },
        {
            path: '/create/group',
            component: AddGroup,
            name: 'AddGroup'
        },
        {
            path: '/login',
            component: Login,
            name: 'Login'
        }
    ]
})