<template>
    <div class="login">
        <from class="row form-login">
            <div class="col">
                <h2>Login</h2>
                <div class="form-group">
                    <label for="email">Email</label>
                    <input type="text" id="email" class="form-control" v-model="email">
                </div>
                <div class="form-group">
                    <label for="password">Password</label>
                    <input type="password" id="password" class="form-control" v-model="password">
                </div>
                <div class="error" v-if="check">
                    *Email or password not correct
                </div>
                <div class="btn-group-link">
                    <button class="btn btn-sm btn-primary" @click="login">Login</button>
                    <button class="btn btn-sm btn-secondary" @click="gotoIndex">Cancel</button>
                </div>
            </div>
        </from>
    </div>
</template>

<script>
    import  {HTTP} from "../api/api";

    export default {
        name: 'Login',

        data(){
            return{
                email: null,
                password: null,
                check: false
            }
        },

        methods:{
            login(){
                HTTP.post(`login`, {
                    email: this.email,
                    password: this.password
                })
                    .then(resolve => {
                        document.removeEventListener('keypress', this.listenLogin);
                        this.$parent.username = resolve.data.username;
                        this.$parent.token = resolve.data.token;
                        this.$router.push({name: 'Admin'});
                    })
                    .catch(_ => {
                        this.check = true;
                    })
            },

            gotoIndex(){
                this.$router.push({name: 'Index'});
            },

            listenLogin(ev){
                if (ev.key === 'Enter')
                    this.login();
            }
        },
        created() {
            document.addEventListener('keypress', this.listenLogin);
        }
    }
</script>

<style>
    .error{
        color: red;
    }

    .login{
        width: 50%;
    }
</style>